function setUsername(name) {
  // Firebase prohibited characters: . # $ [ ]
  const invalidChars = /[.#$\[\]]/;
  if (!name || invalidChars.test(name)) {
    alert("Usernames cannot contain . # $ [ ]");
    usernameInput.style.borderColor = 'red';
    return;
  }

  // Check if username already exists in Firebase
  const userWorldRef = ref(database, 'worlds/worldsByUser/' + name);
  get(userWorldRef).then(snapshot => {
    if (snapshot.exists()) {
      alert("This username is already taken. Please choose another.");
      usernameInput.style.borderColor = 'red';
      return;
    } else {
      // Username is unique, proceed
      username = name;
      localStorage.setItem('username', username);
      if (usernameOverlay) usernameOverlay.style.display = 'none';
      if (signUpBackground) signUpBackground.style.display = 'none';
      sanitizeUsername(username);

      const usernameDisplay = document.getElementById('usernameDisplay');
      if (usernameDisplay) {
        usernameDisplay.textContent = `Welcome, ${username}`;
      }
      if (!localStorage.getItem('seenHubTutorial')) {
        startHubTutorial();
      } else {
        const overlay = document.getElementById('tutorialOverlay');
        if (overlay) overlay.style.display = "none";
      }
    }
  });
}

