// Set up controls for grid display
function setGridCameraMode() {
    isGridMode = true;

    // Remove any previous Y-lock listener BEFORE setting camera position
    if (controls._lockYListener) {
        controls.removeEventListener('change', controls._lockYListener);
        controls._lockYListener = null;
    }

    // 1. Set camera and target first
    camera.position.copy(initialHubCamPos);
    controls.target.copy(initialHubTarget);

    // 2. Set all controls parameters
    controls.enableRotate = true;
    controls.rotateSpeed = 0.3;
    controls.enablePan = true;
    controls.panSpeed = 1;
    controls.enableZoom = true;
    controls.screenSpacePanning = true;
    controls.minPolarAngle = Math.PI / 4;
    controls.maxPolarAngle = Math.PI / 4;
    controls.enabled = true;
    controls.mouseButtons.LEFT = THREE.MOUSE.PAN;
    controls.mouseButtons.RIGHT = THREE.MOUSE.ROTATE;
    controls.enableDamping = true;
    controls.dampingFactor = 0.06; // Smooth damping
    controls.minDistance = 10000;
    controls.maxDistance = 10000; // Set this AFTER camera position!


    // Mobile/touch support
    controls.touches.ONE = THREE.TOUCH.PAN;
    controls.touches.TWO = THREE.TOUCH.ROTATE;

    // 3. Now update controls
    controls.update();

    // Now lock Y after camera is set
    const fixedY = camera.position.y;
    controls._lockYListener = () => { camera.position.y = fixedY; };
    controls.addEventListener('change', controls._lockYListener);
}
setGridCameraMode()

function setWorldCameraMode() {
    isGridMode = false;
    controls.enableRotate = true;
    controls.rotateSpeed = 1;
    controls.enablePan = true;
    controls.enableZoom = true;
    controls.minDistance = 0;
    controls.maxDistance = 3200;
    controls.minPolarAngle = 0;
    controls.enableDamping = true;
    controls.maxPolarAngle = Math.PI * 0.5; 
    controls.enabled = true;
    

    controls.mouseButtons.LEFT = null;
    controls.mouseButtons.RIGHT = THREE.MOUSE.ROTATE;

    // mobile support
    controls.touches.ONE = THREE.TOUCH.ROTATE;; 
    controls.touches.TWO = THREE.TOUCH.ROTATE;
  
    // Remove the Y-lock listener if present
    if (controls._lockYListener) {
        controls.removeEventListener('change', controls._lockYListener);
        controls._lockYListener = null;
    }

    controls.update();
}

