function setTutorialBlocker(active) {
  tutorialBlocker = !!active;
  if (canvas) {
    canvas.style.pointerEvents = active ? "none" : "auto";
  }
}

function startHubTutorial() {
  setTutorialBlocker(true); // Block interactions at the start
  let step = 0;
  const overlay = document.getElementById('tutorialOverlay');
  const tooltip = document.getElementById('tutorialTooltip');
  const textDiv = document.getElementById('tutorialText');
  const nextBtn = document.getElementById('tutorialNext');
  const tutorialSkip = document.getElementById('tutorialSkip');

function showStep() {
  const s = hubTutorialSteps[step];
  textDiv.textContent = s.text;
  overlay.style.display = "block";
  document.querySelectorAll('.tutorial-highlight').forEach(el => el.classList.remove('tutorial-highlight'));

  // Show controls images only for the desired step
  if (step === 0) { // Change to your desired step index
    if (window.innerWidth < 768) {
      hubMobileControls.style.display = "block";
    } else {
      hubDesktopControls.style.display = "block";
    }
  }
  else {
    hubMobileControls.style.display = "none";
    hubDesktopControls.style.display = "none";
  }

  if (s.selector) {
    let els = document.querySelectorAll(s.selector);
    if (!els.length) {
      // Wait and retry if the element isn't present yet
      setTimeout(showStep, 200);
      return;
    }
    // Highlight ALL matching elements
    els.forEach(el => {
      el.classList.add('tutorial-highlight');
      el.scrollIntoView({ behavior: "smooth", block: "center" });
    });
  }
}

  nextBtn.onclick = () => {
    step++;
    if (step >= hubTutorialSteps.length) {
      overlay.style.display = "none";
      document.querySelectorAll('.tutorial-highlight').forEach(el => el.classList.remove('tutorial-highlight'));
      localStorage.setItem('seenHubTutorial', '1');
      setTutorialBlocker(false); // Unblock interactions at the end
    } else {
      showStep();
    }
  };
  // Skip button to exit tutorial
  tutorialSkip.onclick = () => {
    overlay.style.display = "none";
    document.querySelectorAll('.tutorial-highlight').forEach(el => el.classList.remove('tutorial-highlight'));
    localStorage.setItem('seenHubTutorial', '1');
    setTutorialBlocker(false); // Unblock interactions if skipped
  };

  showStep();
}


