function playSimpleIntroAnimation(onComplete) {
  // Example: fade in the canvas or move the camera a bit
    gsap.fromTo(canvas, { opacity: 0 }, { opacity: 1, duration: 4.2, onComplete });
  // Or, for a camera move:
  gsap.fromTo(camera.position, { y: 20000 }, { y: 7000, duration: 1.5, ease: "power2.out", onComplete });
}

function playHubIntroCameraAnimation(onComplete) {
  controls.enabled = false;

  // Set the camera to a far/side/above position for a dramatic start
  camera.position.set(0, 20000, 0);
  controls.target.set(0, 0, 0);
  controls.update();

  // Now animate through your path
  const path = [
    { x: 2000, y: 9000, z: 2000, tx: 0, ty: 0, tz: 0 },
    { x: -2000, y: 7000, z: -2000, tx: 0, ty: 0, tz: 0 },
    { x: 1000, y: 7000, z: 1000, tx: 0, ty: 0, tz: 0 }
  ];

  let tl = gsap.timeline({
    onComplete: () => {
      console.log("Camera animation complete");
      controls.enabled = true;
      if (onComplete) onComplete();
    }
  });

  path.forEach((p, i) => {
    tl.to(camera.position, {
      x: p.x, y: p.y, z: p.z,
      duration: 2,
      ease: "power1.inOut",
      onUpdate: () => controls.update()
    }, i * 2);
    tl.to(controls.target, {
      x: p.tx, y: p.ty, z: p.tz,
      duration: 2,
      ease: "power1.inOut",
      onUpdate: () => controls.update()
    }, i * 2);
  });
}

function playCubePlacementDemo() {
  const demoCubes = [];
  const positions = [
    new THREE.Vector3(0, -9950, 0),
    new THREE.Vector3(50, -9950, 0),
    new THREE.Vector3(0, -9950, 50),
    new THREE.Vector3(0, -9950, 100),
    new THREE.Vector3(0, -9950, 150),
    new THREE.Vector3(0, -9900, 150),
    new THREE.Vector3(0, -9850, 150),
    new THREE.Vector3(0, -9800, 150),
    new THREE.Vector3(50, -9800, 150),
    new THREE.Vector3(-50, -9800, 150),
    new THREE.Vector3(0, -9800, 200),
    new THREE.Vector3(0, -9800, 100),
    new THREE.Vector3(0, -9750, 150),
  ];
  positions.forEach((pos, i) => {
    const mat = new THREE.MeshLambertMaterial({ color: 0x7678ED, transparent: true, opacity: 0.6 });
    const cube = new THREE.Mesh(cubeGeo, mat);
    cube.position.copy(pos);
    cube.scale.set(0.01, 0.01, 0.01);
    scene.add(cube);
    demoCubes.push(cube);
    gsap.to(cube.scale, {
      x: 1, y: 1, z: 1,
      duration: 0.4,
      delay: i * 0.3,
      ease: "back.out(2)"
    });
  });
  // Animate removal after a short delay
  setTimeout(() => {
    demoCubes.forEach((cube, i) => {
      gsap.to(cube.scale, {
        x: 0.01, y: 0.01, z: 0.01,
        duration: 0.3,
        delay: i * 0.8,
        onComplete: () => scene.remove(cube)
      });
    });
  }, 1800);
}


