function onGridScroll(event) {
    if (!isGridMode) return; // Only allow in grid mode
    event.preventDefault();

      // Combine both scroll directions for diagonal movement
    const panSpeed = 5;
    // Sum both deltas for a consistent diagonal move
    const delta = (event.deltaX + event.deltaY) * panSpeed * 0.5;

    camera.position.x += delta;
    camera.position.z += delta;
    controls.target.x += delta;
    controls.target.z += delta;
    controls.update();
}

