// css html transitions
function fadeInElement(el, duration = 500) {
  // Cancel any pending fadeOut timeout
  if (el._fadeTimeout) {
    clearTimeout(el._fadeTimeout);
    el._fadeTimeout = null;
  }
  el.style.display = "block";
  setTimeout(() => {
    el.style.opacity = "1";
  }, 100); // Use a short delay to ensure display is set before opacity
}

function fadeOutElement(el, duration = 10) {
  el.style.opacity = "0";
  // Store the timeout so it can be cleared if needed
  el._fadeTimeout = setTimeout(() => {
    el.style.display = "none";
    el._fadeTimeout = null;
  }, duration); // Match your CSS transition duration
}

