const maxSwatches = 16;
const recentSwatches = Array(maxSwatches).fill(""); // or "#eee"

function renderSwatches() {
  const swatchDiv = document.getElementById('recentSwatches');
  swatchDiv.innerHTML = '';
  recentSwatches.forEach(color => {
    const swatch = document.createElement('div');
    if (color) {
      swatch.style.background = color;
      swatch.title = color;
      swatch.onclick = () => colorPicker.color.set(color);
      swatch.style.cursor = "pointer";
    } else {
      swatch.style.background = "transparent";
      swatch.style.border = "2px dashed #888";
      swatch.title = "No color yet";
      swatch.style.cursor = "default";
    }
    swatchDiv.appendChild(swatch);
  });
}

let colorPicked

// Update currentColor on any color change (including swatch clicks)
colorPicker.on('color:change', function(color) {
  colorPicked = color.hexString;
});

// listen to a color picker's color:change event
// color:change callbacks receive the current color
colorPicker.on('input:end', function(color) {
    // log the current color as a HEX string
    colorPicked = color.hexString;
    const hex = color.hexString; // This is a string, not a reference!
    const idx = recentSwatches.indexOf(hex);
    if (idx !== -1) recentSwatches.splice(idx, 1);
    recentSwatches.unshift(hex);
    if (recentSwatches.length > maxSwatches) recentSwatches.pop();
    renderSwatches();
});

