function isOccupied(position) {
  // Checks if a cube already exists at this position
  return myVoxels.some(voxel => voxel.position.equals(position));
}

function isAdjacentToCubeOrFloor(position) {
  // Allow placement if the cube is sitting directly on the floor (y == -9975)
  if (Math.abs(position.y - (-9975)) < 1e-3) return true;
  // Check for adjacent cubes
  const offsets = [
    new THREE.Vector3(50, 0, 0),
    new THREE.Vector3(-50, 0, 0),
    new THREE.Vector3(0, 50, 0),
    new THREE.Vector3(0, -50, 0),
    new THREE.Vector3(0, 0, 50),
    new THREE.Vector3(0, 0, -50),
  ];
  return offsets.some(offset => {
    const neighborPos = position.clone().add(offset);
    return myVoxels.some(voxel => voxel.position.equals(neighborPos));
  });
}

function onPointerMove(event) {
  if (tutorialBlocker) return;
  if (pickColorMode) {
    rollOverMesh.visible = false;
    canvas.style.cursor = "crosshair";
    return;
  }

  pointer.set((event.clientX / window.innerWidth) * 2 - 1, -(event.clientY / window.innerHeight) * 2 + 1);
  raycaster.setFromCamera(pointer, camera);

  // Hover over selector mesh?
  const selectorIntersects = raycaster.intersectObjects(worldSelectors, false);
  if (selectorIntersects.length > 0) {
    document.body.style.cursor = "pointer";
    rollOverMesh.visible = false;
    return;
  }

  const planeIntersect = raycaster.intersectObject(worldPlane, false);
  if (planeIntersect.length > 0) {
    mouseWorldX = planeIntersect[0].point.x;
    mouseWorldZ = planeIntersect[0].point.z;
  }

  const intersects = raycaster.intersectObjects(objects, false);

  if (intersects.length > 0) {
    const intersect = intersects[0];

    if (currentEditWorldUsername !== username && getCurrentContributionCount() >= 10) {
      rollOverMesh.visible = false;
      return;
    }

    if (deleteModeActive) {
      rollOverMesh.visible = false;
      return;
    }

    // Calculate intended position (snap to grid)
    const intendedPosition = intersect.point.clone().add(intersect.face.normal);
    intendedPosition.divideScalar(50).floor().multiplyScalar(50).addScalar(25);

    // Check if position is already occupied
    if (isOccupied(intendedPosition)) {
      rollOverMesh.visible = false;
      return;
    }

    // Check adjacency (must be adjacent to floor or another cube)
    if (!isAdjacentToCubeOrFloor(intendedPosition)) {
      rollOverMesh.visible = false;
      return;
    }

    // If all checks pass, show the rollOverMesh at the intended position
    rollOverMesh.visible = true;
    canvas.style.cursor = "pointer";
    rollOverMesh.position.copy(intendedPosition);
  } else {
    rollOverMesh.visible = false;
    document.body.style.cursor = "grab";
  }
}

function onPointerDown(event) {
  if (tutorialBlocker) return;
  if (event.button === 2) return; // Right click: rotate only

  pointer.set((event.clientX / window.innerWidth) * 2 - 1, -(event.clientY / window.innerHeight) * 2 + 1);
  raycaster.setFromCamera(pointer, camera);

  // --- COLOR PICK MODE ---
  if (pickColorMode) {
    rollOverMesh.visible = false;
    const intersects = raycaster.intersectObjects([...myVoxels, ...objects], false);
    if (intersects.length > 0) {
      const pickedVoxel = intersects[0].object;
      const pickedColor = pickedVoxel.material.color.getHexString();
      colorPicker.color.set("#" + pickedColor);

      // --- Add picked color to swatches ---
      const hex = "#" + pickedColor;
      const idx = recentSwatches.indexOf(hex);
      if (idx !== -1) recentSwatches.splice(idx, 1);
      recentSwatches.unshift(hex);
      if (recentSwatches.length > maxSwatches) recentSwatches.pop();
      renderSwatches();

      pickColorMode = false;
      colorPickFromCubeBtn.style.background = "";
      colorPickFromCubeBtn.style.color = "";
      canvas.style.cursor = "";
      rollOverMesh.visible = true;
    }
    return; // Don't place/delete cubes in pick mode
  }

  colorPickFromCubeBtn.classList.remove('active');

  // --- SELECTOR MESH CLICK ---
  const selectorIntersects = raycaster.intersectObjects(worldSelectors, false);
  if (selectorIntersects.length > 0) {
    const clickedObj = selectorIntersects[0].object;
    const owner = clickedObj.userData.owner;
    enterWorld(owner);
    return;
  }

  // --- DELETION LOGIC FIRST ---
  const deleteCubesIntersects = raycaster.intersectObjects(myVoxels, false);
  if (deleteModeActive && deleteCubesIntersects.length > 0) {
    const voxel = deleteCubesIntersects[0].object;
    // Only allow deletion if the voxel is unsaved (just placed)
    if (voxel.userData.isUnsaved) {
      const idx = myVoxels.indexOf(voxel);
      if (idx !== -1) {
        gsap.to(voxel.scale, {
          x: 0, y: 0, z: 0,
          duration: 0.3,
          ease: "back.out(2)",
          onComplete: () => {
            scene.remove(voxel);
            myVoxels.splice(idx, 1);
            updateWorldSenderState();
            updateCubeDeleteModeState();

            // Update the counter immediately after deletion
            if (currentEditWorldUsername !== username) {
              if(window.innerWidth < 768) {
                counterDivMobile.textContent = `Your contributions: ${getCurrentContributionCount()}/10`;
              fadeInElement(counterDivMobile, 1000);
              }
              else{
              counterDiv.textContent = `Your contributions: ${getCurrentContributionCount()}/10`;
              fadeInElement(counterDiv, 1000);
              }
                
              
            }
          }
        });

        // Remove from world array by matching position (or matrix)
        for (let i = 0; i < world.length; i++) {
          const matrix = new THREE.Matrix4().fromArray(world[i].matrix);
          const pos = new THREE.Vector3();
          matrix.decompose(pos, new THREE.Quaternion(), new THREE.Vector3());
          if (pos.distanceTo(voxel.position) < 1e-3) {
            world.splice(i, 1);
            break;
          }
        }
      }
    } else {
      // Optional: show a message or shake the cube to indicate it's not deletable
      // e.g., gsap.to(voxel.position, { x: "+=5", yoyo: true, repeat: 3, duration: 0.05 });
    }
    return; // Prevent further actions
  }

  // --- ONLY BLOCK PLACEMENT IF AT LIMIT ---
  if (currentEditWorldUsername !== username && getCurrentContributionCount() >= 10) {
      if(window.innerWidth < 768) {
                  counterDivMobile.classList.remove('maxCubeCountWarningDeactivated');
    counterDivMobile.classList.add('maxCubeCountWarning');
              }
              else{
                counterDiv.classList.remove('maxCubeCountWarningDeactivated');
    counterDiv.classList.add('maxCubeCountWarning');
              }
  
    return;
  }

  // --- CUBE PLACEMENT ---
  const intersects = raycaster.intersectObjects(objects, false);
  if (intersects.length > 0) {
    const intersect = intersects[0];

    if (deleteModeActive) return; // Don't allow placing cubes in delete mode

    // check if going out of buildable space
    if (
      intersect.object.position.x > 450 ||
      intersect.object.position.x < -450 ||
      intersect.object.position.z > 450 ||
      intersect.object.position.z < -450 ||
      intersect.object.position.y > 950
    ) {
      console.log("clicking over the limit on x or z or y");
      return;
    }

    // Calculate intended position (snap to grid)
    const intendedPosition = intersect.point.clone().add(intersect.face.normal);
    intendedPosition.divideScalar(50).floor().multiplyScalar(50).addScalar(25);

    // Check if position is already occupied
    if (isOccupied(intendedPosition)) {
      return;
    }

    // Check adjacency (must be adjacent to floor or another cube)
    if (!isAdjacentToCubeOrFloor(intendedPosition)) {
      return;
    }

    // create cube
    const voxel = new THREE.Mesh(cubeGeo, singleCubeMaterial.clone());
    voxel.material.color.set(colorPicked);
    voxel.userData.owner = username;
    voxel.position.copy(intendedPosition);
    voxel.scale.set(0.01, 0.01, 0.01);
    scene.add(voxel);
    voxel.userData.isUnsaved = true;
    voxel.userData.origColor = {
      r: voxel.material.color.r,
      g: voxel.material.color.g,
      b: voxel.material.color.b
    };
    gsap.to(voxel.scale, {
      x: 1, y: 1, z: 1,
      duration: 0.3,
      ease: "back.out(2)",
      onComplete: () => {
        voxel.updateMatrix();
        voxel.updateMatrixWorld();
        myVoxels.push(voxel);
        objects.push(voxel);

        if (currentEditWorldUsername !== username) {
           if(window.innerWidth < 768) {
                  counterDivMobile.textContent = `Your contributions: ${getCurrentContributionCount()}/10`;
                  fadeInElement(counterDivMobile, 1000);
              }
              else{
                counterDiv.textContent = `Your contributions: ${getCurrentContributionCount()}/10`;
                fadeInElement(counterDiv, 1000);
              }
          showCubePlusOne();
        }

        updateWorldSenderState();
        updateCubeDeleteModeState();

        // Save the matrix without the -10000 offset
        const adjustedMatrix = voxel.matrix.clone();
        const adjustedPosition = new THREE.Vector3();
        const adjustedQuaternion = new THREE.Quaternion();
        const adjustedScale = new THREE.Vector3();
        adjustedMatrix.decompose(adjustedPosition, adjustedQuaternion, adjustedScale);

        // Remove the -10000 offset from the y position
        adjustedPosition.y += 10000;
        adjustedMatrix.compose(adjustedPosition, adjustedQuaternion, adjustedScale);

        world.push({
          matrix: adjustedMatrix.toArray(),
          color: voxel.material.color.getHex()
        });
      }
    });
  }
}
