function enterWorld(targetUsername) {
  // Clear personal area
  myVoxels.forEach(voxel => scene.remove(voxel));
  myVoxels = [];
  world = [];
  myContributionCount = 0;
  currentEditWorldUsername = targetUsername;


  if (window._myContribListener) {
      window._myContribListener(); // Detach previous listener
      window._myContribListener = null;
  }

  // Ensure UI elements are visible
  fadeInElement(colorPickerCss, 500);
  
  fadeInElement(leavePersonalWorldButton, 500);
  fadeInElement(worldSender, 500);
  fadeInElement(cubeDeleteMode, 500);
  fadeInElement(worldSenderAndGoToHubHolder, 500);
  fadeOutElement(aboutIcon, 500);

  if (window.innerWidth < 768) {
    fadeInElement(openColorPickerBtn, 500);
  }
  else{
    fadeInElement(colorContainer, 500);
  }

  // Set camera and transition
  setWorldCameraMode();
  transitionToPrivateWorld();

  const userWorldRef = ref(database, 'worlds/worldsByUser/' + targetUsername);
  const contribRef = ref(database, `worlds/contributions/${targetUsername}`);
  const myContribRef = ref(database, `worlds/contributions/${targetUsername}/${username}`);

  window._myContribListener = onValue(myContribRef, (snap) => {
    let count = 0;
    if (snap.exists()) {
        try {
            const arr = JSON.parse(snap.val());
            if (Array.isArray(arr)) count = arr.length;
        } catch (e) {}
    }
    myContributionCount = count;
    if (targetUsername !== username) {
        counterDiv.textContent = `Your contributions: ${myContributionCount}/10`;
        counterDivMobile.textContent = `Your contributions: ${myContributionCount}/10`;

    }
});

  // Check if the world exists
  get(userWorldRef).then(worldSnap => {
    if (!worldSnap.exists() && targetUsername === username) {
      // Create an empty world for the user if it doesn't exist
      set(userWorldRef, JSON.stringify([]))
        .then(() => {
          console.log("Created an empty world for user:", username);
          // Reload the world after creating it
          enterWorld(username);
        })
        .catch(error => {
          console.error("Failed to create an empty world:", error);
        });
      return; // Exit early to avoid running the rest of the logic
    }

    // If the world exists or it's not the user's own world, proceed to load it
    Promise.all([get(userWorldRef), get(contribRef), get(myContribRef)]).then(([worldSnap, contribSnap, myContribSnap]) => {
      // Load owner's cubes
      if (worldSnap.exists()) {
        try {
          const worldArray = JSON.parse(worldSnap.val());
          if (Array.isArray(worldArray)) {
            if (worldArray.length === 0) {
              console.log("World is empty. Ready for user to add cubes.");
            } else {
              worldArray.forEach(objJson => {
                const voxel = new THREE.Mesh(cubeGeo, singleCubeMaterial.clone());
                if (objJson.color !== undefined && objJson.color !== null && objJson.color !== "") {
                  // If it's a number, use setHex
                  if (typeof objJson.color === "number") {
                    voxel.material.color.setHex(objJson.color);
                  } else {
                    voxel.material.color.set(objJson.color);
                  }
                  // Fix for white
                  const hex = voxel.material.color.getHexString().toLowerCase();
                  if (hex === "ffffff" || hex === "fff") {
                    voxel.material.color.set("#fefefe");
                  }
                  // Do NOT override true black ("000000")
                } else {
                  voxel.material.color.set("#ff0000"); // Only set red if color is missing/invalid
                }
                voxel.userData.origColor = {
                  r: voxel.material.color.r,
                  g: voxel.material.color.g,
                  b: voxel.material.color.b
                };
                if (objJson.matrix && Array.isArray(objJson.matrix) && objJson.matrix.length === 16) {
                  voxel.matrix.fromArray(objJson.matrix);
                  voxel.matrix.decompose(voxel.position, voxel.quaternion, voxel.scale);
                }
                voxel.userData.owner = targetUsername; // Tag as owner
                voxel.userData.origColor = {
                  r: voxel.material.color.r,
                  g: voxel.material.color.g,
                  b: voxel.material.color.b
                };
                voxel.position.y += -10000;
                scene.add(voxel);
                myVoxels.push(voxel);
                objects.push(voxel);
              });
            }
          }
        } catch (e) {
          console.error("Failed to load world:", e);
        }
      }

      // Load contributions
      if (contribSnap.exists()) {
        contribSnap.forEach(contribUserSnap => {
          try {
            const contribArray = JSON.parse(contribUserSnap.val());
            if (Array.isArray(contribArray)) {
              contribArray.forEach(objJson => {
                const voxel = new THREE.Mesh(cubeGeo, singleCubeMaterial.clone());
                if (objJson.color !== undefined && objJson.color !== null && objJson.color !== "") {
                  // If it's a number, use setHex
                  if (typeof objJson.color === "number") {
                    voxel.material.color.setHex(objJson.color);
                  } else {
                    voxel.material.color.set(objJson.color);
                  }
                  // Fix for white
                  const hex = voxel.material.color.getHexString().toLowerCase();
                  if (hex === "ffffff" || hex === "fff") {
                    voxel.material.color.set("#fefefe");
                  }
                  // Do NOT override true black ("000000")
                } else {
                  voxel.material.color.set("#ff0000"); // Only set red if color is missing/invalid
                }
                if (objJson.matrix && Array.isArray(objJson.matrix) && objJson.matrix.length === 16) {
                  voxel.matrix.fromArray(objJson.matrix);
                  voxel.matrix.decompose(voxel.position, voxel.quaternion, voxel.scale);
                }
                voxel.userData.owner = contribUserSnap.key; // Tag as contributor
                voxel.userData.origColor = {
                  r: voxel.material.color.r,
                  g: voxel.material.color.g,
                  b: voxel.material.color.b
                };
                voxel.position.y += -10000;
                scene.add(voxel);
                myVoxels.push(voxel);
                objects.push(voxel);
              });
            }
          } catch (e) {
            console.error("Failed to load contributions:", e);
          }
        });
      }

      // Count your own contribution for this world
      myContributionCount = 0;
      if (myContribSnap.exists()) {
        try {
          const myContribArray = JSON.parse(myContribSnap.val());
          if (Array.isArray(myContribArray)) {
            myContributionCount = myContribArray.length;
          }
        } catch (e) {
          console.error("Failed to count contributions:", e);
        }
      }

      // Update contributors list
      get(contribRef).then(contribSnap => {
        let contributors = [];
        if (contribSnap.exists()) {
          contribSnap.forEach(contribUserSnap => {
            if (contribUserSnap.key !== targetUsername) {
              contributors.push(contribUserSnap.key);
            }
          });
        }

        fadeInElement(contribDiv, 500);
        const totalCubes = myVoxels.length + (contribSnap.exists() ? contribSnap.size : 0);
        let html = `<b>Owner:</b> <span class="contrib-name contrib-owner" data-user="${targetUsername}" style="font-weight:bold;cursor:pointer;">${targetUsername}</span>
        <br><b>Total cubes:</b> <span data-user="${totalCubes}">${totalCubes} </span>
        <br><b>Contributors:</b><br>`;
        
        if (contributors.length > 0) {
          html += contributors
            .map(u => `<span class="contrib-name" data-user="${u}" style="cursor:pointer;">${u}</span>`)
            .join('<br>');
            
        } else {
          html += '<i>No contributors yet</i>';
        }
       
 
        contribDiv.innerHTML = html;

        // Add event listeners for highlight on click
        document.querySelectorAll('.contrib-name').forEach(span => {
          span.addEventListener('click', () => {
            myVoxels.forEach(v => v.userData._highlighted = false);
            highlightVoxels(span.dataset.user, true);
          });
        });
      });

      // Update cube counter
      if (targetUsername === username) {
        counterDiv.style.display = 'none';
        counterDivMobile.style.display = 'none';
      } else {
         if(window.innerWidth < 768) {
                  fadeInElement(counterDivMobile, 1000);
        counterDivMobile.textContent = `Your contributions: ${myContributionCount}/10`;
              }
              else{
                fadeInElement(counterDiv, 1000);
        counterDiv.textContent = `Your contributions: ${myContributionCount}/10`;
              }
        
        
      }
    });
  });
}

