// 1. Define your animation patterns as functions
function animateColumnByColumn(instancedMesh, jumpHeight) {
  let minX = Infinity, maxX = -Infinity;
  for (let i = 0; i < instancedMesh.count; i++) {
    const orig = instancedMesh.userData.originalPositions[i];
    minX = Math.min(minX, orig.x);
    maxX = Math.max(maxX, orig.x);
  }
  for (let i = 0; i < instancedMesh.count; i++) {
    const orig = instancedMesh.userData.originalPositions[i];
    const matrix = new THREE.Matrix4();
    instancedMesh.getMatrixAt(i, matrix);
    const pos = orig.clone();
    const quat = new THREE.Quaternion();
    const scl = new THREE.Vector3();
    matrix.decompose(new THREE.Vector3(), quat, scl);

    const normX = (orig.x - minX) / (maxX - minX);
    const delay = normX * 0.25;
    gsap.to(pos, {
      y: orig.y + jumpHeight,
      duration: 0.25,
      delay: delay,
      ease: "back.out(2)",
      onUpdate: () => {
        const newMatrix = new THREE.Matrix4();
        newMatrix.compose(pos, quat, scl);
        instancedMesh.setMatrixAt(i, newMatrix);
        instancedMesh.instanceMatrix.needsUpdate = true;
      }
    });
  }
}

function animatePopcorn(instancedMesh, jumpHeight) {
  for (let i = 0; i < instancedMesh.count; i++) {
    const orig = instancedMesh.userData.originalPositions[i];
    const matrix = new THREE.Matrix4();
    instancedMesh.getMatrixAt(i, matrix);
    const pos = orig.clone();
    const quat = new THREE.Quaternion();
    const scl = new THREE.Vector3();
    matrix.decompose(new THREE.Vector3(), quat, scl);

    const delay = Math.random() * 0.25;
    const ease = "back.out(" + (1.5 + Math.random() * 2) + ")";
    gsap.to(pos, {
      y: orig.y + jumpHeight,
      duration: 0.25,
      delay: delay,
      ease: ease,
      onUpdate: () => {
        const newMatrix = new THREE.Matrix4();
        newMatrix.compose(pos, quat, scl);
        instancedMesh.setMatrixAt(i, newMatrix);
        instancedMesh.instanceMatrix.needsUpdate = true;
      }
    });
  }
}

function animateExplosion(instancedMesh) {
  // Find center
  let center = new THREE.Vector3();
  for (let i = 0; i < instancedMesh.count; i++) {
    center.add(instancedMesh.userData.originalPositions[i]);
  }
  center.divideScalar(instancedMesh.count);

  for (let i = 0; i < instancedMesh.count; i++) {
    const orig = instancedMesh.userData.originalPositions[i].clone();
    const matrix = new THREE.Matrix4();
    instancedMesh.getMatrixAt(i, matrix);
    const pos = orig.clone();
    const quat = new THREE.Quaternion();
    const scl = new THREE.Vector3();
    matrix.decompose(new THREE.Vector3(), quat, scl);

    const dir = orig.clone().sub(center).normalize();
    const dist = 200 + Math.random() * 120;
    const target = orig.clone().add(dir.multiplyScalar(dist));
    target.y += 120 + Math.random() * 60;

    gsap.to(pos, {
      x: target.x,
      y: target.y,
      z: target.z,
      duration: 0.5,
      ease: "expo.out",
      onUpdate: () => {
        const newMatrix = new THREE.Matrix4();
        newMatrix.compose(pos, quat, scl);
        instancedMesh.setMatrixAt(i, newMatrix);
        instancedMesh.instanceMatrix.needsUpdate = true;
      }
    });
  }
}

