// World sender button functionality
function showConfettiBurst() {
  const confettiCount = 120;
  const geometry = new THREE.BufferGeometry();
  const positions = [];
  const colors = [];
  const color = new THREE.Color();
  for (let i = 0; i < confettiCount; i++) {
    positions.push(0, -10000, 0);
    color.setHSL(Math.random(), 0.7, 0.6);
    colors.push(color.r, color.g, color.b);
  }
  geometry.setAttribute('position', new THREE.Float32BufferAttribute(positions, 3));
  geometry.setAttribute('color', new THREE.Float32BufferAttribute(colors, 3));
  const material = new THREE.PointsMaterial({ size: 80, vertexColors: true, transparent: true, opacity: 1, sizeAttenuation: true });
  const points = new THREE.Points(geometry, material);
  scene.add(points);

  for (let i = 0; i < confettiCount; i++) {
    const angle = Math.random() * Math.PI * 2;
    const speed = 800 + Math.random() * 1200;
    const ySpeed = 400 + Math.random() * 800;
    const start = { x: 0, y: -10000, z: 0 };
    const end = { x: Math.cos(angle) * speed, y: -11000 + ySpeed, z: Math.sin(angle) * speed };
    gsap.to(start, {
      x: end.x,
      y: end.y,
      z: end.z,
      duration: 1.2,
      ease: "power2.out",
      onUpdate: () => {
        geometry.attributes.position.setXYZ(i, start.x, start.y, start.z);
        geometry.attributes.position.needsUpdate = true;
      },
      onComplete: () => {
        if (i === confettiCount - 1) {
          scene.remove(points);
          geometry.dispose();
          material.dispose();
        }
      }
    });
  }
  gsap.to(material, { opacity: 0, duration: 1.2, delay: 0.7 });
}
